@media screen and (max-width: 1025px) {
  .menu_logo--text {
    display: none;
  }

  .menu_item--p {
    display: none;
  }

  .menu_logo {
    border-bottom: 1px solid #353f61;
    padding-bottom: 18px;
  }
}

@media screen and (max-width: 915px) {
  .content_block--height {
    height: 89.75%;
  }

  .content_block--height2 {
    height: 82.4%;
  }
}

@media screen and (max-width: 769px) {
  .advisors_td p {
    font-size: 11px;
  }

  .advisors_btns {
    display: none;
  }

  .content_top--title {
    font-size: 18px;
    margin-left: 45px;
  }

  .content_top {
    padding-left: 30px;
  }

  .generate_col,
  .generate_col2:nth-child(1),
  .generate_col2:nth-child(5) {
    width: 100%;
  }
}

@media screen and (max-width: 600px) {
  .intro_form--input {
    width: 100%;
  }
}